.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  height: 150px;
  padding: 10px;
  margin: 15px auto;
  border-radius: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background: linear-gradient(to left, #f5f5f5, #f5f5f5); /* Cambiado a un gradiente más claro */
  transition: transform 0.3s ease-in-out;

}

.card:hover {
  transform: scale(1.05);
  cursor: pointer;
  background: linear-gradient(to left, #ffffff, #ffffff);
}

img {
  width: 9rem;
  height: 6rem;
  object-fit: contain; /* Ajusta el tamaño de la imagen sin distorsionar */
  border-radius: 50%;
  margin-bottom: 10px;
}

h3 {
  margin: 0;
  font-size: 1.5rem;
  color: #6b6b6b; /* Color de texto más oscuro para mejorar la legibilidad */
}

.title {
  font-size: 1.2rem;
  color: #777; /* Color más claro para el título */
  margin-top: 5px;
}